html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-check {
  width: 1.23em;
  height: 1em;
  fill: #E8663C;
}

.icon-cog {
  width: 1em;
  height: 1em;
  fill: #E8663C;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-user {
  width: 1.05em;
  height: 1em;
  fill: #E8663C;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: "bebas";
  src: url("../fonts/bebas.woff2") format("woff2"), url("../fonts/bebas.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: Helvetica, sans-serif;
}

.button {
  font-size: 12px;
  text-transform: uppercase;
  height: 32px;
  padding: 0 13px;
  display: inline-block;
  color: #ffffff;
  text-decoration: none;
  background-color: #e8603c;
  line-height: 32px;
  font-family: bebas, sans-serif;
}

.h3 {
  display: flex;
  font-size: 18px;
  font-weight: normal;
  color: #e8603c;
  text-transform: uppercase;
  text-decoration: none;
  margin-bottom: 23px;
  font-family: bebas, sans-serif;
}

.h3:after {
  content: "";
  flex: 1;
  margin-left: 20px;
  background: url(../img/pattern.png) 0 0;
}

input[type="text"] {
  color: #e8603c;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    margin: 0 10px;
  }
}

.slider {
  position: relative;
}

.slider__slide {
  height: 0;
  padding-bottom: 42%;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 43%;
  left: 3%;
  cursor: pointer;
  z-index: 100;
  font-size: 54px;
}

.slider__next {
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

.slide {
  position: relative;
  height: 0;
  padding-bottom: 42%;
}

.slide .slide__content {
  position: absolute;
  padding: 20px 30px;
  background: rgba(232, 102, 60, 0.8);
  left: 37px;
  bottom: 31px;
  max-width: 416px;
}

@media only screen and (max-width: 620px) {
  .slide .slide__content {
    background: none;
    top: 10px;
    bottom: auto;
  }
}

.slide__title {
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  margin-bottom: 14px;
  font-family: bebas, sans-serif;
}

.slide p {
  font-size: 12px;
  line-height: 1.4;
  color: #ffffff;
}

.hero {
  overflow: hidden;
}

.hero__wrap {
  position: relative;
}

.hero__wrap:before, .hero__wrap:after {
  position: absolute;
  height: 60px;
  background: #f2ae9c;
  content: "";
  width: 1000px;
  top: 0;
}

.hero__wrap:before {
  right: 100%;
  margin-right: 10px;
}

.hero__wrap:after {
  left: 100%;
  margin-left: 10px;
}

.thumbs {
  margin-top: 14px;
}

.thumbs ul {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 17px;
}

@media only screen and (max-width: 620px) {
  .thumbs ul {
    grid-template-columns: repeat(2, 1fr);
  }
}

.thumbs a {
  display: block;
  height: 0;
  text-decoration: none;
  padding-bottom: 60%;
  background-size: cover;
  background-position: 50% 50%;
}

.features {
  margin-top: 38px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 17px;
  margin-bottom: 48px;
}

@media only screen and (max-width: 840px) {
  .features {
    display: block;
  }
}

@media only screen and (max-width: 840px) {
  .features .feature {
    margin-bottom: 50px;
  }
}

.features .feature__title {
  display: flex;
  margin-bottom: 32px;
}

@media only screen and (max-width: 840px) {
  .features .feature__title {
    display: block;
    margin-bottom: 10px;
  }
}

.features .feature__icon {
  flex: 0;
  margin-right: 18px;
}

@media only screen and (max-width: 840px) {
  .features .feature__icon {
    margin-bottom: 10px;
  }
}

.features .feature__icon .icon {
  font-size: 46px;
  fill: #e8603c;
}

.features .feature h2 {
  color: #e8603c;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: normal;
  line-height: 1.35;
  flex: 1;
  font-family: bebas, sans-serif;
}

.features .feature__text {
  margin-top: 32px;
  font-size: 14px;
  line-height: 1.5;
  color: #414141;
  margin-bottom: 24px;
  padding-right: 20px;
}

@media only screen and (max-width: 600px) {
  .features .feature__text {
    margin: 17px 0;
  }
}

.features .feature__text p {
  margin-bottom: 1em;
}

.works {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 17px;
  margin-bottom: 48px;
}

@media only screen and (max-width: 700px) {
  .works {
    grid-template-columns: repeat(2, 1fr);
  }
}

.works .work {
  display: block;
  text-decoration: none;
}

.works .work__image img {
  display: block;
  min-width: 100%;
}

.works .work__title {
  font-size: 16px;
  text-transform: uppercase;
  color: #b9b9b9;
  margin-bottom: 6px;
}

.works .work__bottom {
  border: 1px solid #dbdbdb;
  border-top: none;
  padding: 15px;
  font-family: bebas, sans-serif;
}

.works .work__date {
  color: #e8603c;
  font-size: 11px;
  text-transform: uppercase;
}

.page-title {
  margin-bottom: 26px;
}

.page-title h1 {
  background: #e8603c;
  font-size: 22px;
  font-weight: normal;
  color: #ffffff;
  text-transform: uppercase;
  padding: 15px;
  font-family: bebas, sans-serif;
}

.contact-content {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-gap: 17px;
  margin-bottom: 38px;
}

@media only screen and (max-width: 700px) {
  .contact-content {
    display: block;
  }
}

.contact-content__map iframe {
  min-width: 100%;
  height: 400px;
  margin-bottom: 38px;
}

.contact-content__info {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.contact-content__info--address {
  font-size: 14px;
  line-height: 1.4;
  padding-right: 10px;
}

.contact-content__info--subscribe p input[type="text"] {
  font-family: bebas, sans-serif;
  color: #e8603c;
  width: 50%;
  border: 1px solid #e8603c;
  padding: 12px 14px;
  text-transform: uppercase;
  margin-bottom: 11px;
}

.contact-content__info--subscribe p input[type="submit"] {
  font-family: bebas, sans-serif;
  color: #ffffff;
  background-color: #e8603c;
  width: 70px;
  padding: 10px 14px;
  text-transform: uppercase;
}

.contact-content__info--subscribe p textarea {
  font-family: bebas, sans-serif;
  height: 206px;
  min-height: 118px;
  width: 100%;
  border: 1px solid #e8603c;
  padding: 12px 14px;
  text-transform: uppercase;
  margin-bottom: 11px;
}

.header__wrap {
  display: flex;
  text-align: left;
  padding: 25px 0;
  align-items: baseline;
}

@media only screen and (max-width: 620px) {
  .header__wrap {
    display: block;
    text-align: center;
  }
}

@media only screen and (max-width: 620px) {
  .header__logo {
    margin-bottom: 20px;
  }
}

.header__nav {
  margin-left: auto;
}

@media only screen and (max-width: 620px) {
  .header__nav {
    top: 0;
    text-align: center;
    line-height: 2;
  }
}

.logo {
  font-size: 0;
  color: #e8603c;
  text-decoration: none;
  font-family: bebas, sans-serif;
}

.logo__part1, .logo__part2 {
  display: inline-block;
  font-size: 36px;
}

.logo__part2 {
  color: #f3af9d;
}

.nav {
  font-size: 16px;
  font-family: bebas, sans-serif;
}

.nav a {
  text-decoration: none;
  color: #b9b9b9;
  margin-left: 24px;
}

.nav a.is-active, .nav a:hover {
  color: #e8603c;
}

.footer {
  color: #fff;
  font-size: 12px;
  overflow: hidden;
  line-height: 1.5;
}

.footer__wrap {
  position: relative;
  background: #e8603c;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 17px;
}

@media only screen and (max-width: 600px) {
  .footer__wrap {
    display: block;
  }
}

.footer__wrap:before, .footer__wrap:after {
  position: absolute;
  height: 100%;
  background: #f2ae9c;
  content: "";
  width: 100%;
  top: 0;
}

.footer__wrap:before {
  right: 100%;
  margin-right: 10px;
}

.footer__wrap:after {
  left: 100%;
  margin-left: 10px;
}

.footer__text {
  padding: 0 0 36px 39px;
}

.footer__text p {
  margin-bottom: 1em;
}

.footer h2 {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: normal;
  line-height: 1.5;
  padding: 39px 36px;
  font-family: bebas, sans-serif;
}

@media only screen and (max-width: 500px) {
  .footer h2 {
    margin-bottom: 5px;
  }
}

@media only screen and (max-width: 600px) {
  .footer__col {
    margin-bottom: 20px;
  }
}

.footer__whitelinks a {
  color: #ffffff;
  text-decoration: none;
}

.tweet {
  margin-bottom: 1.2em;
}

.tweet__content {
  margin-bottom: 3em;
}

.tweet a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
}
