@include font('bebas', bebas);

body {
	font-family: Helvetica, sans-serif; }

@mixin bebas {
	font-family: bebas, sans-serif; }

.button {
	font-size: 12px;
	text-transform: uppercase;
	height: 32px;
	padding: 0 13px;
	display: inline-block;
	color: #ffffff;
	text-decoration: none;
	background-color: #e8603c;
	line-height: 32px;
	@include bebas; }

.h3 {
	display: flex;
	font-size: 18px;
	font-weight: normal;
	color: #e8603c;
	text-transform: uppercase;
	text-decoration: none;
	margin-bottom: 23px;
	@include bebas;
	&:after {
		content: "";
		flex: 1;
		margin-left: 20px;
		background: url(../img/pattern.png) 0 0; } }

input[type="text"] {
	color: $orange-color; }
