

.container {
	max-width: 940px;
	margin: 0 auto;
	@include r(960) {
		margin: 0 10px; } }

.slider {
	position: relative;
	&__slide {
		height: 0;
		padding-bottom: 42%; }
	&__next, &__prev {
		position: absolute;
		top: 43%;
		left: 3%;
		cursor: pointer;
		z-index: 100;
		font-size: 54px; }
	&__next {
		left: auto;
		right: 20px;
		transform: rotate(-180deg); } }

.slide {
	position: relative;
	height: 0;
	padding-bottom: 42%;
	.slide__content {
		position: absolute;
		padding: 20px 30px;
		background: rgba(232,102,60,.8);
		left: 37px;
		bottom: 31px;
		max-width: 416px;
		@include r(620) {
			background: none;
			top: 10px;
			bottom: auto; } }
	&__title {
		font-size: 18px;
		color: #ffffff;
		text-decoration: none;
		text-transform: uppercase;
		margin-bottom: 14px;
		@include bebas; }
	p {
		font-size: 12px;
		line-height: 1.4;
		color: #ffffff; } }


.hero {
	overflow: hidden;
	&__wrap {
		position: relative;
		&:before, &:after {
			position: absolute;
			height: 60px;
			background: $pink-color;
			content: "";
			width: 1000px;
			top: 0; }
		&:before {
			right: 100%;
			margin-right: 10px; }
		&:after {
			left: 100%;
			margin-left: 10px; } } }

.thumbs {
	margin-top: 14px;
	ul {
		display: grid;
		grid-template-columns: repeat(6,1fr);
		grid-gap: 17px;
		@include r(620) {
			grid-template-columns: repeat(2,1fr); } }
	a {
		display: block;
		height: 0;
		text-decoration: none;
		padding-bottom: 60%;
		background-size: cover;
		background-position: 50% 50%; } }

.features {
	margin-top: 38px;
	display: grid;
	grid-template-columns: repeat(3,1fr);
	grid-gap: 17px;
	margin-bottom: 48px;
	@include r(840) {
		display: block; }
	.feature {
		@include r(840) {
			margin-bottom: 50px; }
		&__title {
			display: flex;
			margin-bottom: 32px;
			@include r(840) {
				display: block;
				margin-bottom: 10px; } }
		&__icon {
			flex: 0;
			margin-right: 18px;
			@include r(840) {
				margin-bottom: 10px; }
			.icon {
				font-size: 46px;
				fill: $orange-color; } }
		h2 {
			color: $orange-color;
			text-transform: uppercase;
			font-size: 18px;
			font-weight: normal;
			line-height: 1.35;
			flex: 1;
			@include bebas; }
		&__text {
			margin-top: 32px;
			font-size: 14px;
			line-height: 1.5;
			color: #414141;
			margin-bottom: 24px;
			padding-right: 20px;
			@include r(600) {
				margin: 17px 0; }
			p {
				margin-bottom: 1em; } } } }

.works {
	display: grid;
	grid-template-columns: repeat(4,1fr);
	grid-gap: 17px;
	margin-bottom: 48px;
	@include r(700) {
		grid-template-columns: repeat(2,1fr); }
	.work {
		display: block;
		text-decoration: none;
		&__image {
			img {
				display: block;
				min-width: 100%; } }
		&__title {
			font-size: 16px;
			text-transform: uppercase;
			color: #b9b9b9;
			margin-bottom: 6px; }
		&__bottom {
			border: 1px solid #dbdbdb;
			border-top: none;
			padding: 15px;
			@include bebas; }
		&__date {
			color: $orange-color;
			font-size: 11px;
			text-transform: uppercase; } } }


.page-title {
	margin-bottom: 26px;
	h1 {
		background: $orange-color;
		font-size: 22px;
		font-weight: normal;
		color: #ffffff;
		text-transform: uppercase;
		padding: 15px;
		@include bebas; } }

.contact-content {
	display: grid;
	grid-template-columns: 3fr 1fr;
	grid-gap: 17px;
	margin-bottom: 38px;
	@include r(700) {
		display: block; }
	&__map {
		iframe {
			min-width: 100%;
			height: 400px;
			margin-bottom: 38px; } }
	&__info {
		display: grid;
		grid-template-columns: 1fr 2fr;
		&--address {
			font-size: 14px;
			line-height: 1.4;
			padding-right: 10px; }
		&--subscribe {
			p {
				input[type="text"] {
					@include bebas;
					color: $orange-color;
					width: 50%;
					border: 1px solid $orange-color;
					padding: 12px 14px;
					text-transform: uppercase;
					margin-bottom: 11px; }
				input[type="submit"] {
					@include bebas;
					color: #ffffff;
					background-color: $orange-color;
					width: 70px;
					padding: 10px 14px;
					text-transform: uppercase; }
				textarea {
					@include bebas;
					height: 206px;
					min-height: 118px;
					width: 100%;
					border: 1px solid $orange-color;
					padding: 12px 14px;
					text-transform: uppercase;
					margin-bottom: 11px; } } } }
	&__sidebar {} }






